.banner{
    background-size: cover;
    height: 600px;
    color: white;
} 

.content{
    padding-top: 140px;
    height: 190px;
    padding-left: 15px;
    padding-left: 3rem;
}
.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 2rem;
}
.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;   
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(102, 102, 102, 0.459);
    cursor: pointer;
    margin-right: 1rem;
}

.button:hover{
    color: black;
    background-color: #e6e6e6; 
}

.description {
    width: 45rem;
    line-height: 1.3rem;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
}
.fade-bottom{
    height: 17rem;
    background-image: linear-gradient(180deg, transparent, rgba(37,37,37,0.61),#111);
}